import React from 'react';
import { Button, Row, Col, Input, Layout } from 'antd';
import Identicon from 'identicon.js';
import Texty from 'rc-texty';
import QueueAnim from 'rc-queue-anim';
import './App.css';
import md5 from 'blueimp-md5';
import 'rc-texty/assets/index.css';
import TweenOne from 'rc-tween-one';

let appear = () => ({
    opacity: 0,
    y: -60,
    duration: 1500
});

function PTitle(props) {
    return (
        <div>
            <h1 id="pTitle">
                <Texty 
                    type="top"
                    enter={appear}
                >
                    Genvatar
                </Texty>
            </h1>
        </div>
    );
}

class AvatarSetter extends React.Component {
    constructor(props) {
        super(props);
        this.handleInput = this.handleInput.bind(this);
    }

    handleInput(e) {
        this.props.onInputChange(e.target.value);
    }

    render() {
        const userText = this.props.userText;
        return (
                <Input 
                    placeholder="输入用来生成Identicon的字符串" 
                    id="GTextBox"
                    size="large"
                    value={userText}
                    onInput={this.handleInput}
                />
        );
    }
}

class GenerateButton extends React.Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.props.onButtonClick();
    }

    render() {
        return (
            <Button
                type="primary"
                size="large"
                onClick={this.handleClick}
            >
                好！
            </Button>
        );
    }
}

class AvatarViewer extends React.Component {
    render() {
        const avatarImg = this.props.avatarImg;
        return (
            <div>
                {avatarImg}
            </div>           
        );
    }
}

class BgShapes extends React.Component {
    render() {
        return (
            <React.Fragment>
                <TweenOne
                    animation={{
                        x: 10,
                        y: -30,
                        scale: 0.97,
                        repeat: -1,
                        duration: 20000,
                        yoyo: true,
                    }}
                >
                <div id="shape1" />
                </TweenOne>
                <TweenOne
                    animation={{
                        x: -30,
                        y: 10,
                        scale: 0.97,
                        repeat: -1,
                        duration: 10000,
                        yoyo: true,
                    }}
                >
                <div id="shape2" />
                </TweenOne>
            </React.Fragment>
        );
    }
}

export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userText: '',
            avatarSize: 144,
            avatarImg: (
                <img 
                    src={'data:image/png;base64,' + generateAvatar(new Date(), 144)}
                    alt="Avatar" width="144px"
                />
            ),
        };
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleGenerate = this.handleGenerate.bind(this);
    }

    handleInputChange(userText) {
        this.setState({
            userText: userText,
        })
    }

    handleGenerate() {
        const userText = this.state.userText;
        let pngFile = generateAvatar(userText, 144);
        this.setState({
            avatarImg: (
                <img 
                    src={'data:image/png;base64,' + pngFile}
                    alt="Avatar" width="144px"
                />
            ),
            ifDisplay: true,
        });
    }

    render() {
        const userText = this.state.userText;
        const avatarSize = this.state.avatarSize;
        const avatarImg = this.state.avatarImg;
        return (
            <div>
            <BgShapes />
            <Layout style={{ 
                "paddingTop": "24vh", 
                "backgroundColor": "#f2f8fe",
                "height": "100vh"
            }}>
            <QueueAnim
                type="top"
                duration="2000"
            >
                <Row gutter={[0, 16]} key="Row1">
                    <Col span={24}><PTitle /></Col>
                </Row>
                <Row
                    justify="center"
                    gutter={[0, 24]}
                    key="Row2"
                >
                    <Col span={4}>
                        
                        <AvatarViewer 
                            avatarImg={avatarImg}
                        />
                    </Col>
                </Row>
                <Row 
                    justify="center"
                    gutter={[0, 24]}
                    key="Row3"
                >
                    <Col>
                        <AvatarSetter 
                            userText={userText}
                            onInputChange={this.handleInputChange}
                        />
                    </Col>
                </Row>
                <Row 
                    justify="center"
                    gutter={[0, 24]}
                    key="Row4"
                >
                    <Col span={2}><GenerateButton onButtonClick={this.handleGenerate}/></Col>
                </Row>
            </QueueAnim>
            </Layout>
            </div>
        );
    }
}

let colorBoard = [
    [255, 197,  61, 255],
    [250, 219,  20, 255],
    [160, 217,  17, 255],
    [115, 209, 61, 255],
    [135, 232, 222, 255],
    [19, 194, 194, 255],
    [105, 192, 255, 255],
    [24, 144, 255, 255],
    [192, 108, 1, 255],
    [47, 84, 235, 255],
    [133, 165, 255, 255],
    [146, 84, 222, 255],
    [255, 173, 210, 255],
    [255, 120, 117, 255],
    [255, 169, 64, 255],
    [139, 0, 18, 255]
];

function generateAvatar(text, size) {
    let hash = md5(text);
    let avatarColor = 0;
    for(let i = 28; i < 32; ++i) {
        avatarColor = avatarColor ^ parseInt(hash.charAt(i), 16);
    }
    let options = {
        foreground: colorBoard[avatarColor],
        background: [245, 245, 250, 255],
        margin: 0,
        size: size,
    };
    return new Identicon(hash.slice(0, 28), options).toString();
}